<template>
  <div name="functional-location">
    <expandable-section-std title="Functional Location" ref="expandFunctionLocation" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <lookups-std v-model="emoc.Functional_Location__c"
                          v-validate="'required'"
                          data-vv-as="Functional Location"
                          data-vv-value-path="inputValue"
                          label="Functional Location"
                          placeholder="Search..."
                          :options="optionsLookupFunctionalLocation()"
                          :required="true"
                          :error="errors.has('Functional_Location__c')"
                          name="Functional_Location__c"
                          @search="filterLookupFunctionalLocation(arguments[0])"
                          @setDataRow="setDataRow(arguments[0])"
                          icon="account"
                          :disabled="disabledNextStaEmocRegis"
                          loading>
              <template #error>
                <span>{{ errors.first('Functional_Location__c') }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Functional_Location_Description__c" label="Functional Location Description" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Plant_of_Main_Work_Center__c" label="Plant of Main Work Center" disabled></input-text-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Main_Work_Center__c" label="Main Work Center" disabled></input-text-std>
          </div>
        </div>
        <div v-if="emocEditEnable" class="row">
          <div class="col-md-6">
            <lookups-std v-model="emoc.Requestor_Name__c"
                         v-validate="'required'"
                         data-vv-as="Requester"
                         data-vv-value-path="inputValue"
                         label="Requester"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         :required="true"
                         :error="errors.has('Requestor_Name__c')"
                         name="Requestor_Name__c"
                         @search="filterLookupUser(arguments[0])"
                         @setDataRow="setDataRow2(arguments[0])"
                         icon="people"
                         :disabled="disabledNextStaEmocRegis"
                         loading>
              <template #error>
                <span>{{ errors.first('Requestor_Name__c') }}</span>
              </template>
            </lookups-std>
          </div>
          <div class="col-md-6">
            <input-text-std v-model="emoc.Indicator__c" label="Indicator" disabled></input-text-std>
          </div>
        </div>
      <!--   <div v-if="emocEditEnable" class="row">
          <div class="col-md-6">
            <input-text-std v-model="emoc.Requestor_ID__c" label="Requestor ID" disabled></input-text-std>
          </div>
        </div>
        <div class="row">
          <div class="col-md-12">
              <input-text-std
                v-model="emoc.Description__c"
                v-validate="'required|max:40'"
                data-vv-as="Description"
                data-vv-value-path="inputValue"
                name="Description__c"
                label="Description"
                :error="errors.has('Description__c')"
                :required="true"
                :maxlength="40"
                :disabled="disabledNextStaEmocRegis"
              >
                <template #error>
                  <span>{{ errors.first('Description__c') }}</span>
                </template>
              </input-text-std>
            </div>
          </div> -->
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'functional-location',
  props: [
    'emoc',
    'emocEditEnable',
    'disabledNextStaEmocRegis'
  ],
  data () {
    return {
      objectOptionsExample: [
        {
          value: '6a978413-76b9-44c7-9ca4-bde3178382aa',
          label: 'Accounts',
          icon: 'standard:account',
          disabled: false
        }
      ],
      stringOptions: ['Accounts', 'Contacts', 'Leads', 'Groups', 'Files', 'Reports', 'Products', 'Users'],
      value: null,
      lookupType: 'main'
    }
  },
  created () {
    if (this.emoc.Functional_Location__r.Name !== '' && this.emoc.Functional_Location__r.Name) {
      this.filterLookupFunctionalLocation(this.emoc.Functional_Location__r.Name)
    }
    if (this.emoc.Requestor_Name__c !== '' && this.emoc.Requestor_Name__c) {
      this.filterLookupUser(this.emoc.Requestor_Name__c)
    }
  },
  computed: {
    lookupFncLocation () { return this.$store.state.lookupFncLocation },
    lookupUser () { return this.$store.state.lookupUser }
    // optionsLookupUser () {
    //   return this.lookupUser.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.FirstName + ' ' + dm.LastName,
    //       icon: 'people'
    //     }
    //   })
    // }
  },
  watch: {
    // 'emoc.Requestor_Name__c' (newVal) {
    //   if (newVal !== '' && newVal) {
    //     this.filterLookupUser(newVal)
    //     let defaultFomula = this.lookupUser.filter(df => {
    //       return df.Id === newVal
    //     })
    //     if (defaultFomula.length !== 0) {
    //       this.emoc.Department_Resp__c = defaultFomula[0].ORGID__c || this.emoc.Department_Resp__c
    //       this.emoc.Indicator__c = defaultFomula[0].Indicator__c || this.emoc.Indicator__c
    //       this.emoc.Requestor_ID__c = defaultFomula[0].Employee_Id__c
    //     }
    //   } else {
    //     this.emoc.Department_Resp__c = ''
    //     this.emoc.Indicator__c = ''
    //     this.emoc.Requestor_ID__c = ''
    //     this.filterLookupUser('')
    //   }
    // },
    'emoc.Functional_Location__c' (newVal) {
      if (this.emoc.Functional_Location__r.Name !== '' && this.emoc.Functional_Location__r.Name) {
        this.filterLookupFunctionalLocation(this.emoc.Functional_Location__r.Name)
      }
    },
    'emoc.Requestor_Name__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    filterLookupFunctionalLocation (searchParam) {
      this.$store.dispatch('receiveLookupFncLocation', { type: this.lookupType, sendParam: searchParam })
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupFunctionalLocation () {
      if (typeof this.lookupFncLocation[this.lookupType] !== 'undefined') {
        return this.lookupFncLocation[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.Name,
            icon: 'account'
          }
        })
      } else {
        return []
      }
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    },
    setDataRow (dataRow) {
      this.emoc.Functional_Location_Description__c = dataRow.Description__c
      this.emoc.Plant_of_Main_Work_Center__c = dataRow.mainWorkCenterPlant_Text__c
      this.emoc.Main_Work_Center__c = dataRow.MainWorkCenter_Text__c
      if (typeof dataRow.Functional_Location__c === 'undefined') {
        this.filterLookupFunctionalLocation('')
      }
    },
    setDataRow2 (dataRow) {
      this.emoc.Department_Resp__c = dataRow.ORGID__c || this.emoc.Department_Resp__c
      this.emoc.Indicator__c = dataRow.Indicator__c
      this.emoc.Requestor_ID__c = dataRow.Employee_Id__c
      if (typeof dataRow.Functional_Location__c === 'undefined') {
        this.filterLookupUser('')
      }
    }
  }
}
</script>


