<template>
  <div name="return-function-override">
    <expandable-section-std title="Return Functional Override back to Normal" ref="expandReturnFunctionOverride" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6">
            <checkbox-std
              v-model="emoc.Return_to_Normal__c"
              label="Return to Normal"
              :inline="true"
              :disabled="disabledReturnFunctionOverride" 
              @onChange="setActionBy(emoc.Return_to_Normal__c)"
            />
          </div>
          <div class="col-md-6">
            <lookups-std v-model="emoc.Action_by_return_to_normal__c"
                         label="Action by"
                         placeholder="Search..."
                         :options="optionsLookupUser()"
                         name="Action_by_return_to_normal__c"
                         @search="filterLookupUser(arguments[0])"
                         icon="people"
                         disabled
                         loading>
            </lookups-std>
          </div>
        </div>
        <div class="row">
          <div class="col-6" v-if="showClearanceNumber">
            <lookups-std v-model="emoc.Clearance_Number_Final__c"
                         v-validate="setRuleClearanceFinal"
                         data-vv-as="Reference clearance certificate number for return the change back to normal."
                         data-vv-value-path="inputValue"
                         label="Reference clearance certificate number for return the change back to normal"
                         placeholder="Search..."
                         :options="optionsLookupClearanceNumber"
                         :error="errors.has('Clearance_Number_Final__c')"
                         name="Clearance_Number_Final__c"
                         @search="filterLookupClearanceNumber(arguments[0])"
                         icon="custom"
                         loading
                         :required="setShowRequired"
                         :disabled="disabledReturnFuncClearanceNumber" >
              <template #error>
                <span>{{ errors.first('Clearance_Number_Final__c') }}</span>
              </template>
            </lookups-std>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>

export default {
  name: 'return-function-override',
  props: [
    'emoc', 'disabledReturnFunctionOverride', 'disabledReturnFuncClearanceNumber'
  ],
  data () {
    return {
      nameRules: [
        v => !!v || 'Name is required'
      ],
      lookupType: 'returnFuncOver'
    }
  },
  created () {
    if (this.emoc.Inhibit_for_process_or_Equipment_S_D__c || this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Inhibit_Alarm_request_for_FOX__c) {
      this.filterLookupClearanceNumber(this.emoc.Clearance_Number_Final__c)
    }
    if (this.emoc.Action_by_return_to_normal__c !== '' && this.emoc.Action_by_return_to_normal__c) {
      this.filterLookupUser(this.emoc.Action_by_return_to_normal__c)
    }
  },
  computed: {
    lookupClearanceNumber () { return this.$store.state.lookupClearanceNumber },
    optionsLookupClearanceNumber () {
      return this.lookupClearanceNumber.map(dm => {
        return {
          value: dm.Work_Clearance__c,
          label: dm.Work_Clearance__c,
          icon: 'custom'
        }
      })
    },
    lookupUser () { return this.$store.state.lookupUser },
    // optionsLookupUser () {
    //   return this.lookupUser.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.FirstName + ' ' + dm.LastName,
    //       icon: 'people'
    //     }
    //   })
    // }
    showClearanceNumber () {
      if (this.emoc.Return_to_Normal__c === true && (this.emoc.Inhibit_for_process_or_Equipment_S_D__c || this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Inhibit_Alarm_request_for_FOX__c || this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)) {
        return true
      } else {
        this.emoc.Clearance_Number_Final__c = ''
        return false
      }
    },
    setRuleClearanceFinal () {
      if (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Implementation#2') {
        return 'required'
      } else if (this.emoc.Status_Functional_Override__c === 'Return to normal condition' && this.emoc.flgClearanceNumberFinal === false) {
        return 'required'
      } else {
        return ''
      }
    },
    setShowRequired () {
      if (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Implementation#2') {
        return true
      } else if (this.emoc.Status_Functional_Override__c === 'Return to normal condition' && this.emoc.flgClearanceNumberFinal === false) {
        return true
      } else {
        return false
      }
    }
  },
  watch: {
    'emoc.Clearance_Number_Final__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupClearanceNumber(newVal)
      }
    },
    'emoc.Action_by_return_to_normal__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    setActionBy (val) {
      this.emoc.Action_by_return_to_normal__c = (this.emoc.Return_to_Normal__c === true) ? this.emoc.User_Login : ''
      if (typeof this.emoc.Action_by_return_to_normal__c !== 'undefined' && this.emoc.Action_by_return_to_normal__c !== '') {
        this.filterLookupUser(this.emoc.User_Login)
      }
    },
    filterLookupClearanceNumber (searchParam) {
      this.$store.dispatch('receiveLookupClearanceNumber', searchParam)
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    }
  }
}
</script>


