<template>
  <div name="prelim-risk-assessment">
    <expandable-section-std title="Prelim Risk assessment" ref="expandPrelimRiskAssessment" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-6">
            <div class="row">
              <div class="col-1" v-if="!disabledNextStaPrelim" ></div>
              <div class="col-11" :class="{'col-12': disabledNextStaPrelim}" >
                <combobox-std
                  v-model="emoc.Potential_Risk_c__c"
                  v-validate="'required'"
                  data-vv-as="Potential risk"
                  data-vv-value-path="textInputValue"
                  name="Potential_Risk_c__c"
                  :error="errors.has('Potential_Risk_c__c')"
                  :required="true"
                  :options="potentialRiskList"
                  label="Potential risk"
                  :disabled="disabledNextStaPrelim"
                >
                  <template #error>
                    <span>{{ errors.first('Potential_Risk_c__c') }}</span>
                  </template>
                </combobox-std>
              </div>
            </div>
          </div>
          <div class="col-6">
            <textarea-std
              v-model="emoc.Other__c"
              v-validate="setRuleOther"
              data-vv-as="Additional Description of Potential risk"
              data-vv-value-path="inputValue"
              name="Other__c"
              label="Additional Description of Potential risk"
              :error="errors.has('Other__c')"
              :disabled="disabledNextStaPrelim2"
              :rows="1"
            >
              <template #error>
                <span>{{ errors.first('Other__c') }}</span>
              </template>
            </textarea-std>
          </div>
        </div>

        <div class="row">
          <div class="col-12">
            <div v-for="(potentialCon, index) in potentialConsequenceOp" class="row" :key="index">
              <div class="col-6">
                <div class="row">
                  <div v-if="!disabledNextStaPrelim && index > 0" class="col-1 delete-mapping-optional" :class="{'col-1 delete-mapping-optional-disabled': potentialCon.flgDelete}">
                    <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDeleteCon(potentialCon.rowId)" aria-hidden="true"></i>
                  </div>
                  <div v-if="!disabledNextStaPrelim && index === 0" class="col-1  padding-t0" ></div>

                  <div v-show="!potentialCon.flgDelete" class="col-11 padding-t0" :class="{'col-12': disabledNextStaPrelim}" >
                    <combobox-std
                      v-model="potentialCon.Potential_Consequence_c__c"
                      data-vv-scope="potentialConsequence"
                      v-validate="!potentialCon.flgDelete ? 'required' : ''"
                      data-vv-as="Potential consequence"
                      data-vv-value-path="textInputValue"
                      :name="'Potential_Consequence_c__c' + index"
                      :error="errors.has('potentialConsequence.Potential_Consequence_c__c' + index)"
                      :options="potentialConsequenceOption(index)"
                      label="Potential consequence"
                      :disabled="potentialCon.flgDelete || disabledNextStaPrelim"
                      :required="true"
                    >
                      <template #error>
                        <span>{{ errors.first('potentialConsequence.Potential_Consequence_c__c' + index) }}</span>
                      </template>
                    </combobox-std>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-t0" v-show="!potentialCon.flgDelete">
                    <textarea-std
                      v-model="potentialCon.Other_Potential_Consequence__c"
                      data-vv-scope="potentialConsequence"
                      v-validate="(!potentialCon.flgDelete && (potentialCon.Potential_Consequence_c__c === 'อื่นๆ')) ? 'required' : ''" 
                      data-vv-as="Additional Description of Potential consequence"
                      data-vv-value-path="$data.$_value"
                      :name="'Other_Potential_Consequence__c' + index"
                      label="Additional Description of Potential consequence"
                      :error="errors.has('potentialConsequence.Other_Potential_Consequence__c' + index)"
                      :disabled="potentialCon.flgDelete || disabledNextStaPrelim2"
                      :rows="1"
                    >
                      <template #error>
                        <span>{{ errors.first('potentialConsequence.Other_Potential_Consequence__c' + index) }}</span>
                      </template>
                    </textarea-std>
                  </div>
                </div>
              </div>
            </div>
            <div class="row" v-if="!disabledNextStaPrelim">
              <div class="col-sm-6">
                <button class="slds-button slds-button_neutral" @click="clickAddCon"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
              </div>
            </div>
          </div>
        </div>

        <div class="row">
          <div class="col-sm-12">
            <div v-for="(mitigation, index) in mitigationPrecautionOp" class="row" :key="index">
              <div class="col-6">
                <div class="row">

                  <div v-if="!disabledNextStaPrelim && index > 0" class="col-1 delete-mapping-optional" :class="{'delete-mapping-optional-disabled': mitigation.flgDelete}">
                    <i class="fa fa-trash-o icon-delete-mapping-optional" @click="clickDelete(mitigation.rowId)" aria-hidden="true"></i>
                  </div>
                  <div v-if="!disabledNextStaPrelim && index === 0" class="col-1  padding-t0" ></div>

                  <div v-show="!mitigation.flgDelete" class="col-11 padding-t0" :class="{'col-12': disabledNextStaPrelim}" >
                    <combobox-std
                      v-model="mitigation.Mitigation_Precaution_1__c"
                      data-vv-scope="mitigationPrecaution"
                      v-validate="!mitigation.flgDelete ? 'required' : ''"
                      data-vv-as="Mitigation/Precaution"
                      data-vv-value-path="textInputValue"
                      :name="'Mitigation_Precaution_1__c' + index"
                      :error="errors.has('mitigationPrecaution.Mitigation_Precaution_1__c' + index)"
                      :options="mitigationPrecautionOption(index)"
                      label="Mitigation/Precaution"
                      :disabled="mitigation.flgDelete || disabledNextStaPrelim"
                      :required="true"
                    >
                      <template #error>
                        <span>{{ errors.first('mitigationPrecaution.Mitigation_Precaution_1__c' + index) }}</span>
                      </template>
                    </combobox-std>
                  </div>
                </div>
              </div>
              <div class="col-6">
                <div class="row">
                  <div class="col-12 padding-t0" v-show="!mitigation.flgDelete" >
                    <textarea-std
                      v-model="mitigation.Other_Mitigation__c"
                      data-vv-scope="mitigationPrecaution"
                      v-validate="(!mitigation.flgDelete && (mitigation.Mitigation_Precaution_1__c === 'อื่นๆ')) ? 'required' : ''"
                      data-vv-as="Additional Description of Mitigation/Precaution"
                      data-vv-value-path="$data.$_value"
                      :name="'Other_Mitigation__c' + index"
                      label="Additional Description of Mitigation/Precaution"
                      :error="errors.has('mitigationPrecaution.Other_Mitigation__c' + index)"
                      :disabled="mitigation.flgDelete || disabledNextStaPrelim2"
                      :rows="1"
                    >
                      <template #error>
                        <span>{{ errors.first('mitigationPrecaution.Other_Mitigation__c' + index) }}</span>
                      </template>
                    </textarea-std>
                  </div>
                </div>
              </div>
            </div>
            <div class="row" v-if="!disabledNextStaPrelim">
              <div class="col-sm-6">
                <button class="slds-button slds-button_neutral" @click="clickAdd"><i class="fa fa-plus" aria-hidden="true"></i>&nbsp;&nbsp;Add</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>
export default {
  name: 'prelim-risk-assessment',
  props: [
    'emoc',
    'disabledNextStaPrelim',
    'disabledNextStaPrelim2'
  ],
  data () {
    return {}
  },
  created () {
  },
  computed: {
    setRuleOther () {
      return (this.emoc.Potential_Risk_c__c === 'อื่นๆ') ? 'required' : ''
    },
    potentialRiskList () { return this.$store.state.potentialRiskList },
    potentialConsequenceList () { return this.$store.state.potentialConsequenceList },
    potentialConsequence () { return this.$store.state.potentialConsequence },
    potentialConsequenceOp () {
      return this.potentialConsequence.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    },
    mitigationPrecautionList () { return this.$store.state.mitigationPrecautionList },
    mitigationPrecaution () { return this.$store.state.mitigationPrecaution },
    mitigationPrecautionOp () {
      return this.mitigationPrecaution.map((df, idx) => {
        df['rowId'] = idx
        return df
      })
    }
  },
  methods: {
    potentialConsequenceOption (index) {
      let retOption = this.potentialConsequenceList.filter(df => {
        return this.potentialConsequenceOp.map(dm => {
          return dm.Potential_Consequence_c__c
        }).indexOf(df.Potential_Consequence_c__c) === -1 || this.potentialConsequenceOp[index].Potential_Consequence_c__c === df.Potential_Consequence_c__c || df.Potential_Consequence_c__c === 'อื่นๆ'
      }).map(dm => {
        return {value: dm.Potential_Consequence_c__c, label: dm.Potential_Consequence_c__c}
      })
      return retOption
    },
    mitigationPrecautionOption (index) {
      let retOption = this.mitigationPrecautionList.filter(df => {
        return this.mitigationPrecautionOp.map(dm => {
          return dm.Mitigation_Precaution_1__c
        }).indexOf(df.Mitigation_Precaution_1__c) === -1 || this.mitigationPrecautionOp[index].Mitigation_Precaution_1__c === df.Mitigation_Precaution_1__c || df.Mitigation_Precaution_1__c === 'อื่นๆ'
      }).map(dm => {
        return {value: dm.Mitigation_Precaution_1__c, label: dm.Mitigation_Precaution_1__c}
      })
      return retOption
    },
    clickAddCon () {
      this.$validator.validateAll('potentialConsequence')
        .then((result) => {
          if (result) {
            let lastIndex = this.potentialConsequence.length
            this.potentialConsequence.push({
              rowId: lastIndex,
              Id: null,
              E_MOC_Application__c: this.emoc.Id,
              Potential_Consequence_c__c: '',
              Other_Potential_Consequence__c: '',
              Type__c: 'Potential Consequence'
            })
          }
        })
    },
    clickDeleteCon (rowId) {
      let setPotentialConsequence = JSON.parse(JSON.stringify(this.potentialConsequence))
      setPotentialConsequence[rowId]['flgDelete'] = !setPotentialConsequence[rowId]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setPotentialConsequence', setPotentialConsequence)
      })
    },
    clickAdd () {
      this.$validator.validateAll('mitigationPrecaution')
        .then((result) => {
          if (result) {
            let lastIndex = this.mitigationPrecaution.length
            this.mitigationPrecaution.push({
              rowId: lastIndex,
              Id: null,
              E_MOC_Application__c: this.emoc.Id,
              Mitigation_Precaution_1__c: '',
              Other_Mitigation__c: '',
              Type__c: 'Mitigation/Precaution'
            })
          }
        })
    },
    clickDelete (rowId) {
      let setMitigationPrecaution = JSON.parse(JSON.stringify(this.mitigationPrecaution))
      setMitigationPrecaution[rowId]['flgDelete'] = !setMitigationPrecaution[rowId]['flgDelete']
      this.$nextTick(() => {
        this.$store.commit('setMitigationPrecaution', setMitigationPrecaution)
      })
    }
  }
}
</script>
<style scoped>
  .delete-mapping-optional {
    font-size: large;
    color: #0176d3;
    text-align: center;
    padding-top: 26px;
  }
  .delete-mapping-optional-disabled {
    color: rgb(234, 0, 30);
  }
  .delete-mapping-optional
    .icon-delete-mapping-optional {
      cursor: pointer;
    }
  .slds-input-has-icon_right .slds-input {
    text-decoration: line-through;
  }
  .padding-b0{
    padding-bottom: 0px;
  }
  .padding-t0{
    padding-top: 0px;
  }
</style>


