<template>
  <div name="functional-override">
    <functional-location    :emoc="emoc" 
                            :emocEditEnable="emocEditEnable"
                            :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                            ref="functionalLocationCmp" />

    <group                  :emoc="emoc" 
                            :checkRequiredGroup="checkRequiredGroup"
                            :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                            ref="groupCmp" />
  
    <change-type            :emoc="emoc" 
                            :checkRequiredChangeType="checkRequiredChangeType"
                            :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                            :disabledChangeType="disabledChangeType"
                            ref="changeTypeCmp" />

    <prelim-risk-assessment :emoc="emoc"
                            :disabledNextStaPrelim="disabledNextStaPrelim"
                            :disabledNextStaPrelim2="disabledNextStaPrelim2"
                            ref="prelimRiskAssessmentCmp" />
    
    <ram-rating             :emoc="emoc" 
                            :disabledNextStaEmocRegis="disabledRamRatingInitial"
                            ref="ramRatingCmp" />

    <risk-analysis          v-if="showSectionRiskAssessment" 
                            :emoc="emoc"
                            :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                            :disabledRiskAssessment="disabledRiskAssessment"
                            ref="riskAnalysisCmp" />

    <ta2                    v-if="showSectionTa" 
                            :emoc="emoc"
                            :recId="recId"
                            :checkRequiredTASelectionConfirmed="checkRequiredTASelectionConfirmed"
                            :disabledRiskAssessment="disabledRiskAssessment"
                            ref="ta2Cmp" />
                                 
    <technical-review       v-if="showSectionTechnicalReview" 
                            :emoc="emoc"
                            :disabledTechnicalReview="disabledTechnicalReview"
                            ref="technicalReviewCmp" />
    
    <return-function-override v-if="showSectionReturnFunctionOverride" 
                            :emoc="emoc"
                            :disabledReturnFuncClearanceNumber="disabledReturnFuncClearanceNumber"
                            :disabledReturnFunctionOverride="disabledReturnFunctionOverride"
                            ref="returnFunctionOverrideCmp" />

    <extension              v-if="showSectionExtension" 
                            :emoc="emoc"
                            :checkRequiredExtension="checkRequiredExtension"
                            :disabledSectionExtension="disabledSectionExtension"
                            :clickSaveMain="clickSaveMain"
                            ref="extensionCmp" />

    <return-to-normal       v-if="showSectionReturnToNormal" 
                            :emoc="emoc"
                            :disabledReturnToNormal="disabledReturnToNormal"
                            :checkRequiredReturnToNormal="checkRequiredReturnToNormal"
                            ref="returnToNormalCmp" />

  </div>
</template>
<script>
import functionalLocation from './FunctionalLocation.vue'
import group from './Group.vue'
import changeType from './ChangeType.vue'
import prelimRiskAssessment from './PrelimRiskAssessment.vue'
import ramRating from './RamRating.vue'
import extension from './Extension.vue'
import returnToNormal from './ReturnToNormal.vue'
import riskAnalysis from './RiskAnalysis.vue'
import ta2 from './Ta2.vue'
import technicalReview from './TechnicalReview.vue'
import returnFunctionOverride from './ReturnFunctionOverride.vue'
import moment from 'moment'

export default {
  name: 'functional-override',
  props: ['emoc', 'recId', 'emocEditEnable', 'checkRequiredGroup', 'checkRequiredChangeType', 'checkRequiredExtension', 'checkRequiredTASelectionConfirmed', 'checkRequiredReturnToNormal', 'clickSaveMain'],
  data () {
    return {
      items: [],
      currentDate: moment().format('YYYY-MM-DD')
    }
  },
  computed: {
    disabledNextStaEmocRegis () {
      return ['', 'Create'].indexOf(this.emoc.Status_Functional_Override__c) === -1
    },
    disabledRamRatingInitial () {
      return ['', 'Create', 'Approval #1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1
    },
    disabledNextStaPrelim () {
      return ['', 'Create', 'Approval #1', 'Approval #3 (APU)', 'Approval #3 (VP)'].indexOf(this.emoc.Status_Functional_Override__c) === -1
    },
    disabledNextStaPrelim2 () {
      return ['', 'Create', 'Approval #1', 'Approval #3 (APU)', 'Approval #3 (VP)', 'Implementation#1', 'Implementation#2'].indexOf(this.emoc.Status_Functional_Override__c) === -1
    },
    disabledChangeType () {
      return ['', 'Create', 'Implementation#1', 'Implementation#2', 'Return to normal condition'].indexOf(this.emoc.Status_Functional_Override__c) === -1
    },
    disabledReturnFunctionOverride () {
      return this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out'
    },
    disabledReturnFuncClearanceNumber () {
      return (this.emoc.Status_Functional_Override__c === 'Return to normal condition' && this.emoc.flgClearanceNumberFinal) || this.emoc.Status_Functional_Override__c === 'Close out'
    },
    disabledSectionExtension () {
      return this.emoc.Status_Functional_Override__c !== 'Implementation#2'
    },
    disabledReturnToNormal () {
      return this.emoc.Status_Functional_Override__c !== 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out'
    },
    disabledTechnicalReview () {
      return this.emoc.Status_Functional_Override__c !== 'Technical review'
    },
    disabledRiskAssessment () {
      return this.emoc.Status_Functional_Override__c !== 'Risk assessment'
    },
    showSectionExtension () { // น้อยกว่าเท่ากับ
      let showRequireToExtend = moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(0, 'days').format('YYYY-MM-DD')
      let dateDiff = moment(this.currentDate).diff(showRequireToExtend, 'days')
      let dayCheck = 30
      let dateDiffChk = (dateDiff <= 0) ? Math.abs(dateDiff) : dateDiff
      // console.log('dayCheck', dateDiff, dateDiffChk, dayCheck)
      if (this.emoc.Extend_Require_to_extend__c === false && this.emoc.Not_Extend_Require_to_extend__c === false) {
        return (this.emoc.Status_Functional_Override__c === 'Implementation#2') && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true) && dateDiffChk <= dayCheck
      } else {
        return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
      }
    },
    showSectionReturnFunctionOverride () { // มากกว่า 30
      let showRequireToExtend = moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(0, 'days').format('YYYY-MM-DD')
      let dateDiff = moment(this.currentDate).diff(showRequireToExtend, 'days')
      let dayCheck = 30
      let dateDiffChk = (dateDiff <= 0) ? Math.abs(dateDiff) : dateDiff
      if (this.emoc.OOS__c || this.emoc.MOS_for_ops_purpose__c || this.emoc.Inhibit_for_process_or_Equipment_S_D__c) { // type A
        return (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out')
      } else if (this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Disable_Alarm_by_Ops_for_HW__c || this.emoc.Inhibit_Alarm_request_for_FOX__c) { // type B
        return (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Implementation#2' || this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out' || this.emoc.Status_Functional_Override__c === 'Approval #2')
      } else if (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c) { // type C
        if (this.emoc.Status_Functional_Override__c === 'Implementation#2') {
          return dateDiffChk > dayCheck
        } else if (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Approval #2' || this.emoc.Status_Functional_Override__c === 'Approval #3 (APU)' || this.emoc.Status_Functional_Override__c === 'Approval #3 (VP)') {
          return true
        } else {
          return (this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out') && this.emoc.Return_to_Normal__c === true
        }
      }
    },
    showSectionReturnToNormal () {
      return this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out'
    },
    showSectionRiskAssessment () {
      return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
    },
    showSectionTa () {
      return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true) && this.emoc.Show_TA_section__c === true
    },
    showSectionTechnicalReview () {
      return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2', 'Risk assessment'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
    },
    statusRejectCancel () {
      return ['Rejected', 'Cancelled'].indexOf(this.emoc.Status_Functional_Override__c) !== -1
    },
    statusRiskAssessment () {
      return this.emoc.Status_Functional_Override__c === 'Risk assessment'
    },
    statusTechnicalReview () {
      return this.emoc.Status_Functional_Override__c === 'Technical review'
    },
    statusImplementation1 () {
      return this.emoc.Status_Functional_Override__c === 'Implementation#1'
    },
    statusImplement2 () {
      return this.emoc.Status_Functional_Override__c === 'Implementation#2'
    },
    statusReturnToNormal () {
      return this.emoc.Status_Functional_Override__c === 'Return to normal condition'
      // return ['Implementation#2', 'Return to normal condition'].indexOf(this.emoc.Status_Functional_Override__c) !== -1
    }
  },
  watch: {
    'emoc.Status_Functional_Override__c' () {
      if (this.statusRejectCancel) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.isOpen) {
          this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.toggle()
        }
        if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
          this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
        }
        if (this.showSectionExtension) {
          this.$nextTick(() => {
            if (this.$refs.extensionCmp.$refs.expandExtension.isOpen) {
              this.$refs.extensionCmp.$refs.expandExtension.toggle()
            }
          })
        }
        if (this.showSectionReturnFunctionOverride) {
          this.$nextTick(() => {
            if (this.$refs.returnFunctionOverrideCmp.$refs.expandReturnFunctionOverride.isOpen) {
              this.$refs.returnFunctionOverrideCmp.$refs.expandReturnFunctionOverride.toggle()
            }
          })
        }
        if (this.showSectionRiskAssessment) {
          this.$nextTick(() => {
            if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
              this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
            }
          })
        }
        if (this.showSectionTa) {
          this.$nextTick(() => {
            if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
              this.$refs.ta2Cmp.$refs.expandTa2.toggle()
            }
          })
        }
        if (this.showSectionTechnicalReview) {
          this.$nextTick(() => {
            if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
              this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
            }
          })
        }
      } else if (this.statusRiskAssessment) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.isOpen) {
          this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.toggle()
        }
        if (this.showSectionExtension) {
          this.$nextTick(() => {
            if (this.$refs.extensionCmp.$refs.expandExtension.isOpen) {
              this.$refs.extensionCmp.$refs.expandExtension.toggle()
            }
          })
        }
        if (this.showSectionReturnFunctionOverride) {
          this.$nextTick(() => {
            if (this.$refs.returnFunctionOverrideCmp.$refs.expandReturnFunctionOverride.isOpen) {
              this.$refs.returnFunctionOverrideCmp.$refs.expandReturnFunctionOverride.toggle()
            }
          })
        }
      } else if (this.statusTechnicalReview) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.isOpen) {
          this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.toggle()
        }
        if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
          this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
        }
        if (this.showSectionRiskAssessment) {
          this.$nextTick(() => {
            if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
              this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
            }
          })
        }
      } else if (this.statusImplementation1) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.showSectionTa) {
          this.$nextTick(() => {
            if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
              this.$refs.ta2Cmp.$refs.expandTa2.toggle()
            }
          })
        }
        if (this.showSectionTechnicalReview) {
          this.$nextTick(() => {
            if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
              this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
            }
          })
        }
      } else if (this.statusImplement2) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.showSectionRiskAssessment) {
          this.$nextTick(() => {
            if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
              this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
            }
          })
        }
        if (this.showSectionTa) {
          this.$nextTick(() => {
            if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
              this.$refs.ta2Cmp.$refs.expandTa2.toggle()
            }
          })
        }
        if (this.showSectionTechnicalReview) {
          this.$nextTick(() => {
            if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
              this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
            }
          })
        }
      } else if (this.statusReturnToNormal) {
        if (this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.isOpen) {
          this.$refs.functionalLocationCmp.$refs.expandFunctionLocation.toggle()
        }
        if (this.$refs.groupCmp.$refs.expandGroup.isOpen) {
          this.$refs.groupCmp.$refs.expandGroup.toggle()
        }
        if (this.$refs.changeTypeCmp.$refs.expandChangeType.isOpen) {
          this.$refs.changeTypeCmp.$refs.expandChangeType.toggle()
        }
        if (this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.isOpen) {
          this.$refs.prelimRiskAssessmentCmp.$refs.expandPrelimRiskAssessment.toggle()
        }
        if (this.$refs.ramRatingCmp.$refs.expandRamRating.isOpen) {
          this.$refs.ramRatingCmp.$refs.expandRamRating.toggle()
        }
        if (this.showSectionRiskAssessment) {
          this.$nextTick(() => {
            if (this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.isOpen) {
              this.$refs.riskAnalysisCmp.$refs.expandRiskAnalysis.toggle()
            }
          })
        }
        if (this.showSectionTa) {
          this.$nextTick(() => {
            if (this.$refs.ta2Cmp.$refs.expandTa2.isOpen) {
              this.$refs.ta2Cmp.$refs.expandTa2.toggle()
            }
          })
        }
        if (this.showSectionTechnicalReview) {
          this.$nextTick(() => {
            if (this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.isOpen) {
              this.$refs.technicalReviewCmp.$refs.expandTechnicalReview.toggle()
            }
          })
        }
      }
    }
  },
  methods: {
    onAdd () {
      this.items.push('Lorem ipsum dolor sit amet.')
    }
  },
  components: {
    functionalLocation,
    group,
    changeType,
    prelimRiskAssessment,
    ramRating,
    extension,
    returnToNormal,
    riskAnalysis,
    technicalReview,
    ta2,
    returnFunctionOverride
  }
}
</script>


