<template>
  <div name="return-to-normal">
    <expandable-section-std title="ASM is responsible person for this action" ref="expandReturnToNormal" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-12">
            <div class="row" style="padding: 15px;">
              <div class="col-md-6">
                <checkbox-std
                    v-model="emoc.I_have_checked_and_confirmed_the_change__c"
                    label="I have checked and confirmed the change has already returned to normal."
                    :inline="true"
                    class="input-group"
                    @onChange="setActionBy(emoc.I_have_checked_and_confirmed_the_change__c)"
                    :required="true"
                    :disabled="disabledReturnToNormal"
                />
        
              </div>
              <div class="col-md-6">
                <lookups-std v-model="emoc.Action_by_FN_closeout__c"
                             label="Action by"
                             placeholder="Search..."
                             :options="optionsLookupUser()"
                             name="Action_by_FN_closeout__c"
                             @search="filterLookupUser(arguments[0])"
                             icon="people"
                             disabled
                             loading>
                </lookups-std>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div v-show="checkRequiredReturnToNormal" class="slds-form-element slds-has-error">
        <div class="slds-form-element__help" id="error-unique-id-29">
          {{'Return to Normal is required'}}
        </div>
      </div>
    </expandable-section-std>
  </div>
</template>
<script>

export default {
  name: 'return-to-normal',
  props: [
    'emoc', 'disabledReturnToNormal', 'checkRequiredReturnToNormal'
  ],
  data () {
    return {
      nameRules: [
        v => !!v || 'Name is required'
      ],
      lookupType: 'returnNormal'
    }
  },
  created () {
    if (this.emoc.Action_by_FN_closeout__c !== '' && this.emoc.Action_by_FN_closeout__c) {
      this.filterLookupUser(this.emoc.Action_by_FN_closeout__c)
    }
  },
  computed: {
    lookupUser () { return this.$store.state.lookupUser }
    // optionsLookupUser () {
    //   return this.lookupUser.map(dm => {
    //     return {
    //       value: dm.Id,
    //       label: dm.FirstName + ' ' + dm.LastName,
    //       icon: 'people'
    //     }
    //   })
    // }
  },
  watch: {
    'emoc.Action_by_FN_closeout__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupUser(newVal)
      }
    }
  },
  methods: {
    setActionBy (val) {
      this.emoc.Action_by_FN_closeout__c = (this.emoc.I_have_checked_and_confirmed_the_change__c === true) ? this.emoc.User_Login : ''
      if (typeof this.emoc.Action_by_FN_closeout__c !== 'undefined' && this.emoc.Action_by_FN_closeout__c !== '') {
        this.filterLookupUser(this.emoc.User_Login)
      }
    },
    filterLookupUser (searchParam) {
      this.$store.dispatch('receiveLookupUser', {type: this.lookupType, sendParam: searchParam})
    },
    optionsLookupUser () {
      if (typeof this.lookupUser[this.lookupType] !== 'undefined') {
        return this.lookupUser[this.lookupType].map(dm => {
          return {
            value: dm.Id,
            label: dm.FirstName + ' ' + dm.LastName,
            icon: 'people'
          }
        })
      } else {
        return []
      }
    }
  }
}
</script>


