<template>
  <div name="extension">
    <expandable-section-std title="Extension?" ref="expandExtension" :closedOnStart="false">
      <div class="data-frame">
        <div class="row">
          <div class="col-md-6 col-sm-6">
            <checkbox-std
              v-model="emoc.Extend_Require_to_extend__c"
              label="Extend"
              name="Extend_Require_to_extend__c"
              :inline="true"
              :disabled="disabledSectionExtension"
            />
          </div>

          <div class="col-md-6 col-sm-6">
            <checkbox-std
              v-model="emoc.Not_Extend_Require_to_extend__c"
              label="Not Extend"
              :inline="true"
              :disabled="disabledSectionExtension"
            />
            <div v-if="showClearanceNumber">
              <lookups-std v-model="emoc.Clearance_Number_Final_Group_C__c"
                           v-validate="setRuleClearance"
                           data-vv-as="Reference clearance certificate number for return the change back to normal."
                           data-vv-value-path="inputValue"
                           label="Reference clearance certificate number for return the change back to normal"
                           placeholder="Search..."
                           :options="optionsLookupClearanceNumber"
                           :error="errors.has('Clearance_Number_Final_Group_C__c')"
                           name="Clearance_Number_Final_Group_C__c"
                           @search="filterLookupClearanceNumber(arguments[0])"
                           icon="custom"
                           :disabled="disableClearance && disabledSectionExtension"
                           loading
                           :required="!disableClearance" >
                <template #error>
                  <span>{{ errors.first('Clearance_Number_Final_Group_C__c') }}</span>
                </template>
              </lookups-std>
            </div>
          </div>
        </div>
      </div>
    </expandable-section-std>

    <modal-std size="large" ref="refModalExtend" @close="afterClosePopup()" id="extend">
      <template #header>
        <div style="text-align: left;"><span class="slds-card__header-link slds-text-heading_small">Extend MOC</span></div>
      </template>
      <template #content>
        <extend-popup :emoc="emoc"
                      ref="refExtendPopup"/>
      </template>
      <template #footer>
        <div style="text-align: right;">
          <button class="slds-button slds-button_neutral" @click="callBackExtend('Cancel')">Cancel</button>
          <button v-if="btnComfirmDisable" class="slds-button slds-button_brand" @click="callSave('Save')">Comfirm</button>
          <button v-else class="slds-button slds-button_brand" disabled @click="callSave('Save')">Comfirm</button>
        </div>
      </template>
    </modal-std>

    <modal-after-save id="afterSaveExtend"
                      @close="closePopupSuccess"
                      ref="popupAfterSaveExtend"/>

  </div>
</template>
<script>
import extendPopup from './ExtendPopup.vue'

export default {
  name: 'extension',
  props: [
    'emoc',
    'checkRequiredExtension',
    'disabledSectionExtension',
    'clickSaveMain'
  ],
  data () {
    return {
      resultSuccess: {},
      nameRules: [
        v => !!v || 'Name is required'
      ]
    }
  },
  created () {
    if (this.emoc.Not_Extend_Require_to_extend__c && (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)) {
      this.filterLookupClearanceNumber(this.emoc.Clearance_Number_Final_Group_C__c)
    }
  },
  computed: {
    btnComfirmDisable () { return this.$store.state.btnComfirmDisable },
    lookupClearanceNumber () { return this.$store.state.lookupClearanceNumber },
    optionsLookupClearanceNumber () {
      return this.lookupClearanceNumber.map(dm => {
        return {
          value: dm.Work_Clearance__c,
          label: dm.Work_Clearance__c,
          icon: 'custom'
        }
      })
    },
    showClearanceNumber () {
      if (this.emoc.Not_Extend_Require_to_extend__c && (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)) {
        return true
      } else {
        this.emoc.Clearance_Number_Final_Group_C__c = ''
        return false
      }
    },
    setRuleClearance () {
      return (this.emoc.I_have_checked_and_confirmed_the_change__c === true && this.showClearanceNumber === true) ? 'required' : ''
    },
    disableClearance () {
      return !(this.emoc.I_have_checked_and_confirmed_the_change__c === true && this.showClearanceNumber === true)
    }
  },
  watch: {
    'emoc.Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Not_Extend_Require_to_extend__c = false
        if (newValue === true) {
          this.emoc.Comfirm = false
          this.$store.commit('setBtnComfirmDisable', false)
          this.showPopupExtend()
        }
      }
    },
    'emoc.Not_Extend_Require_to_extend__c' (newValue) {
      if (newValue) {
        this.emoc.Extend_Require_to_extend__c = false
        // this.emoc.Reason_for_Extension_temporary_MOC__c = ''
      }
    },
    'emoc.Clearance_Number_Final_Group_C__c' (newVal) {
      if (newVal !== '' && newVal) {
        this.filterLookupClearanceNumber(newVal)
      }
    }
  },
  methods: {
    filterLookupClearanceNumber (searchParam) {
      this.$store.dispatch('receiveLookupClearanceNumber', searchParam)
    },
    showPopupExtend () {
      this.$refs.refModalExtend.onOpen()
    },
    afterClosePopup () {
    },
    callBackExtend (val) {
      this.$nextTick(() => {
        this.$refs.refModalExtend.onClose()
        if (val === 'Cancel') {
          // this.emoc.Reason_for_Extension_temporary_MOC__c = ''
          this.emoc.Extend_Require_to_extend__c = false
        }
      })
    },
    showPopupAfterSave () {
      this.$refs.popupAfterSaveExtend.onOpen()
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.emoc.Id
      })
    },
    callSave (val) {
      // this.emoc.End_date__c = (this.emoc.newEndDateTempoHis !== '') ? this.emoc.newEndDateTempoHis : null
      this.emoc.FN_TA_Review_Date__c = null
      this.callBackExtend(val)
      this.clickSaveMain()
    }
  },
  components: {
    extendPopup
  }
}
</script>


