<template>
  <div class="create-emoc">
    <div class="slds-card__header slds-grid header custom-border-header">
      <header class="slds-media slds-media_center slds-has-flexi-truncate">
        <div class="slds-media__body" >
          <h2>
            <span class="slds-card__header-link slds-text-heading_small">Edit E-MOC</span>
          </h2>
        </div>
      </header>
    </div>

    <modal-waiting      ref="popupLoader"/>

    <modal-after-save   @close="closePopupSuccess"
                        ref="popupAfterSave"/>

    <functional-override :emocEditEnable="emocEditEnable"
                         :emoc="emoc"
                         :recId="recId"
                         :checkRequiredGroup="checkRequiredGroup"
                         :checkRequiredChangeType="checkRequiredChangeType"
                         :checkRequiredExtension="checkRequiredExtension"
                         :checkRequiredTASelectionConfirmed="checkRequiredTASelectionConfirmed"
                         :checkRequiredReturnToNormal="checkRequiredReturnToNormal"
                         :clickSaveMain="clickSave"
                         ref="functionalOverride"/>

    <footer class="slds-card__footer">
      <div class="row">
        <div class="col-md-4"></div>
        <div class="col-md-4">
          <popover-error-std v-show="hasError"
                             :errorBagItems="errorBagItems"
                             :getLabelFields="getLabelFields"
                             :focusField="focusField"
                             ref="popoverErrorStd"/>
        </div>
        <div class="col-md-4 btn-footer">
          <button class="slds-button slds-button_brand" @click="clickSave">Save</button>
        </div>
      </div>
    </footer>
  </div>
</template>
<script>
import functionalOverride from './FunctionalOverride.vue'
import moment from 'moment'

export default {
  name: 'create-emoc',
  data () {
    return {
      numpage: 3,
      hasError: false,
      errorBagItems: [],
      recId: '',
      resultSuccess: {},
      cpmCapexNextFalse: false,
      currentDate: moment().format('YYYY-MM-DD'),
      indexOfRam: ['L/N', 'L', 'M', 'H']
    }
  },
  created () {
    this.$store.dispatch('receiveLookupFncLocation', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupUser', { type: 'main', sendParam: '' })
    this.$store.dispatch('receiveLookupClearanceNumber', '')
    this.$store.dispatch('receivePotentialRiskList')
    this.$store.dispatch('receivePotentialConsequenceList')
    this.$store.dispatch('receiveMitigationPrecautionList')
  },
  mounted () {
    const queryString = window.location.search
    const urlParams = new URLSearchParams(queryString)
    this.recId = urlParams.get('id')
    let paramSearch = {
      emocApplicationId: this.recId
    }
    if (this.emocEditEnable) {
      this.$store.dispatch('receiveEmocApplication', paramSearch)
      this.$store.dispatch('receivePotentialConsequence', paramSearch)
      this.$store.dispatch('receiveMitigationPrecaution', paramSearch)
    }
  },
  computed: {
    emoc () { return this.$store.state.emoc },
    emocEditEnable () {
      return this.recId && this.recId !== ''
    },
    checkRequiredGroup () {
      return (this.emoc.Id !== '' && this.emoc.Id) && !(this.emoc.OOS__c || this.emoc.MOS_for_ops_purpose__c || this.emoc.Inhibit_for_process_or_Equipment_S_D__c || this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Disable_Alarm_by_Ops_for_HW__c || this.emoc.Inhibit_Alarm_request_for_FOX__c || this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c)
    },
    checkRequiredChangeType () {
      return (this.emoc.Id !== '' && this.emoc.Id) && !(this.emoc.Temporary__c)
    },
    checkRequiredTASelectionConfirmed () {
      return false
      // return (this.showSectionTa2 === true) && !(this.emoc.TA_Selection_confirmed__c)
    },
    showSectionExtension () {
      let showRequireToExtend = moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(0, 'days').format('YYYY-MM-DD')
      let dateDiff = moment(this.currentDate).diff(showRequireToExtend, 'days')
      let dayCheck = 30
      let dateDiffChk = (dateDiff <= 0) ? Math.abs(dateDiff) : dateDiff
      console.log('dayCheck', dateDiff, dateDiffChk, dateDiffChk <= dayCheck)
      if (this.emoc.Extend_Require_to_extend__c === false && this.emoc.Not_Extend_Require_to_extend__c === false) {
        return (this.emoc.Status_Functional_Override__c === 'Implementation#2') && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true) && dateDiffChk <= dayCheck
      } else {
        return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
      }
    },
    showSectionReturnFunctionOverride () {
      let showRequireToExtend = moment(this.emoc.End_date__c, 'DD/MM/YYYY').add(0, 'days').format('YYYY-MM-DD')
      let dateDiff = moment(this.currentDate).diff(showRequireToExtend, 'days')
      let dayCheck = 30
      let dateDiffChk = (dateDiff <= 0) ? Math.abs(dateDiff) : dateDiff
      if (this.emoc.OOS__c || this.emoc.MOS_for_ops_purpose__c || this.emoc.Inhibit_for_process_or_Equipment_S_D__c) { // type A
        return (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out')
      } else if (this.emoc.Inhibit_alarm_for_maintenance__c || this.emoc.Disable_Alarm_by_Ops_for_HW__c || this.emoc.Inhibit_Alarm_request_for_FOX__c) { // type B
        return (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Implementation#2' || this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out' || this.emoc.Status_Functional_Override__c === 'Approval #2')
      } else if (this.emoc.MOS_for_maintenance__c || this.emoc.Safeguarding_force_signal__c) { // type C
        if (this.emoc.Status_Functional_Override__c === 'Implementation#2') {
          return dateDiffChk > dayCheck
        } else if (this.emoc.Status_Functional_Override__c === 'Implementation#1' || this.emoc.Status_Functional_Override__c === 'Approval #2' || this.emoc.Status_Functional_Override__c === 'Approval #3 (APU)' || this.emoc.Status_Functional_Override__c === 'Approval #3 (VP)') {
          return true
        } else {
          return (this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out') && this.emoc.Return_to_Normal__c === true
        }
      }
    },
    checkRequiredExtension () {
      return (this.showSectionExtension === true) && !(this.emoc.Extend_Require_to_extend__c || this.emoc.Not_Extend_Require_to_extend__c)
    },
    showSectionTa2 () {
      return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true) && this.emoc.Show_TA_section__c === true
    },
    showSectionTechnicalReview () {
      return (['', 'Create', 'Approval #1', 'Implementation#1', 'Approval #2', 'Risk assessment'].indexOf(this.emoc.Status_Functional_Override__c) === -1) && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
    },
    showSectionReturnToNormal () {
      return this.emoc.Status_Functional_Override__c === 'Return to normal condition' || this.emoc.Status_Functional_Override__c === 'Close out'
    },
    checkRequiredReturnToNormal () {
      return (this.showSectionReturnToNormal === true) && !(this.emoc.I_have_checked_and_confirmed_the_change__c)
    },
    showSectionRiskAssessment () {
      return ['Risk assessment'].indexOf(this.emoc.Status_Functional_Override__c) !== -1 && (this.emoc.MOS_for_maintenance__c === true || this.emoc.Safeguarding_force_signal__c === true)
    },
    checkRamFinal () {
      if (this.emoc.RAM_Total__c && this.emoc.RAM_Total_Final__c) {
        return this.indexOfRam.indexOf(this.emoc.RAM_Total_Final__c) <= this.indexOfRam.indexOf(this.emoc.RAM_Total__c)
      } else {
        return true
      }
    },
    checkRamInitialH () {
      if (this.emoc.RAM_Total_Final__c && this.emoc.RAM_Total__c === 'H') {
        return this.emoc.RAM_Total_Final__c !== 'H'
      } else {
        return true
      }
    }
  },
  watch: {
    recId (newVal) {
      if (newVal !== '' && newVal) {
        this.numpage = 3
      }
    }
  },
  methods: {
    showPopup () {
      this.$refs.popupAfterSave.onOpen()
    },
    closePopup () {
      this.$refs.popupAfterSave.onClose()
    },
    getLabelFields (field) {
      let listFieldsLabel = {
        Functional_Location__c: 'Functional Location',
        Completion_date__c: 'Required Completion date',
        Start_date__c: 'Required Start date',
        End_date__c: 'Required End date',
        Description__c: 'Description', // Fn
        Reason_of_Work__c: 'Reason of Work', // Fn
        Description_of_change__c: 'Description of change',
        Reason_of_change__c: 'Reason of change',
        Justification__c: 'Justification',
        Requestor_Name__c: 'Requester',
        Type_A_Maintenance_budget__c: 'Budget Type',
        Normal__c: 'MOC Type',
        Temporary__c: 'Change Type',
        Assets_Severity__c: 'Assets Severity',
        Assets_Likelihood__c: 'Assets Likelihood',
        Environment_Severity__c: 'Environment Severity',
        Environment_Likelihood__c: 'Environment Likelihood',
        Potential_Risk_c__c: 'Potential Risk', // Fn
        Potential_Consequence_c__c: 'Potential Consequence', // Fn
        Mitigation_Precaution_1__c: 'Mitigation/Precaution', // Fn
        People_Severity__c: 'People Severity',
        People_Likelihood__c: 'People Likelihood',
        Product_Quality_Severity__c: 'Product Quality Severity',
        Product_Quality_Likelihood__c: 'Product Quality Likelihood',
        Reputation_Severity__c: 'Reputation Severity',
        Reputation_Likelihood__c: 'Reputation Likelihood',
        Assets_Severity_Final__c: 'Assets Severity',
        Assets_Likelihood_Final__c: 'Assets Likelihood',
        Environment_Severity_Final__c: 'Environment Severity',
        Environment_Likelihood_Final__c: 'Environment Likelihood',
        People_Severity_Final__c: 'People Severity',
        People_Likelihood_Final__c: 'People Likelihood',
        Product_Quality_Severity_Final__c: 'Product Quality Severity',
        Product_Quality_Likelihood_Final__c: 'Product Quality Likelihood',
        Reputation_Severity_Final__c: 'Reputation Severity',
        Reputation_Likelihood_Final__c: 'Reputation Likelihood',
        Mechanical_change__c: 'Category Type & Sub-Category Type',
        MOC_Focal_Point_Name__c: 'MOC Focal Point Name',
        HAZOP__c: 'Risk Analysis',
        Extend_Require_to_extend__c: 'Extend',
        Not_Extend_Require_to_extend__c: 'Not Extend',
        OOS__c: 'Functional override categories',
        TA_Selection_confirmed__c: 'TA Selection confirmed',
        // Reason_for_Extension_temporary_MOC__c: 'Reason of extension',
        I_have_checked_and_confirmed_the_change__c: 'I have checked and confirmed the change has already returned to normal.',
        SIL_Level__c: 'SIL Level',
        Design_Intent__c: 'Design Intent',
        Initiating_Event_Demand_Scenario__c: 'Initiating Event (Demand Scenario)',
        Consequence_of_Failure_on_Demand__c: 'Consequence of Failure on Demand',
        Mitigation_Plan_1__c: 'Mitigation Plan #1 (Preventive, Before Top Event Happen)',
        Mitigation_Plan_2__c: 'Mitigation Plan #2 (Mitigative, After Top Event Happen)',
        btnHelpTextRamFinal: !this.checkRamInitialH ? 'Final Risk cannot higher than Initial risk or H' : 'Final Risk cannot higher than Initial risk',
        Clearance_Number_Final__c: 'Reference clearance certificate number for return the change back to normal',
        Clearance_Number_Final_Group_C__c: 'Reference clearance certificate number for return the change back to normal.',
        Work_Clearance_c__c: 'Reference clearance certificate number'
      }
      if (field.indexOf('TA2_Indicator') !== -1) {
        return 'Indicator / Name'
      } else if (field.indexOf('Potential_Consequence_c__c') !== -1) {
        return 'Potential Consequence'
      } else if (field.indexOf('Other_Potential_Consequence__c') !== -1) {
        return 'Additional Description of Potential consequence'
      } else if (field.indexOf('Mitigation_Precaution_1__c') !== -1) {
        return 'Mitigation/Precaution'
      } else if (field.indexOf('Other_Mitigation__c') !== -1) {
        return 'Additional Description of Mitigation/Precaution'
      } else {
        return listFieldsLabel[field]
      }
    },
    focusField (target) {
      let elements = document.getElementsByName(target)
      let refFuctionLocation = this.$refs.functionalOverride.$refs.functionalLocationCmp
      let refGroup = this.$refs.functionalOverride.$refs.groupCmp
      let refChangeType = this.$refs.functionalOverride.$refs.changeTypeCmp
      let refPrelimRiskAssessment = this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp
      let refRamRating = this.$refs.functionalOverride.$refs.ramRatingCmp
      let refExtension = this.$refs.functionalOverride.$refs.extensionCmp
      let refReturnToNormal = this.$refs.functionalOverride.$refs.returnToNormalCmp
      let refReturnFunctionOverride = this.$refs.functionalOverride.$refs.returnFunctionOverrideCmp
      let refRiskAnalysis
      if (this.showSectionRiskAssessment) {
        refRiskAnalysis = this.$refs.functionalOverride.$refs.riskAnalysisCmp
      }
      let ta2
      if (this.showSectionTa2) {
        refRiskAnalysis = this.$refs.functionalOverride.$refs.riskAnalysisCmp
        ta2 = this.$refs.functionalOverride.$refs.ta2Cmp
      }
      if (['Functional_Location__c'].indexOf(target) !== -1 && !refFuctionLocation.$refs.expandFunctionLocation.isOpen) {
        refFuctionLocation.$refs.expandFunctionLocation.toggle()
      } else if ([
        'OOS__c',
        'MOS_for_ops_purpose__c',
        'Inhibit_for_process_or_Equipment_S_D__c',
        'Inhibit_alarm_for_maintenance__c',
        'Disable_Alarm_by_Ops_for_HW__c',
        'Inhibit_Alarm_request_for_FOX__c',
        'MOS_for_maintenance__c',
        'Safeguarding_force_signal__c',
        'Work_Clearance_c__c'].indexOf(target) !== -1 && !refGroup.$refs.expandGroup.isOpen) {
        refGroup.$refs.expandGroup.toggle()
      } else if (['Temporary__c', 'Start_date__c', 'End_date__c', 'Reason_of_Work__c'].indexOf(target) !== -1 && !refChangeType.$refs.expandChangeType.isOpen) {
        refChangeType.$refs.expandChangeType.toggle()
      } else if ([
        'Potential_Risk_c__c',
        'Other__c',
        'Potential_Consequence_c__c',
        'Other_Potential_Consequence__c',
        'Mitigation_Precaution_1__c',
        'Other_Mitigation__c'].indexOf(target) !== -1 && target.indexOf('Potential_Consequence_c__c') !== -1 && !refPrelimRiskAssessment.$refs.expandPrelimRiskAssessment.isOpen) {
        refPrelimRiskAssessment.$refs.expandPrelimRiskAssessment.toggle()
      } else if ([
        'Assets_Severity__c', 'Assets_Likelihood__c',
        'Environment_Severity__c', 'Environment_Likelihood__c',
        'People_Severity__c', 'People_Likelihood__c',
        'Product_Quality_Severity__c', 'Product_Quality_Likelihood__c',
        'Reputation_Severity__c', 'Reputation_Likelihood__c',
        'Assets_Severity_Final__c', 'Assets_Likelihood_Final__c',
        'Environment_Severity_Final__c', 'Environment_Likelihood_Final__c',
        'People_Severity_Final__c', 'People_Likelihood_Final__c',
        'Product_Quality_Severity_Final__c', 'Product_Quality_Likelihood_Final__c',
        'Reputation_Severity_Final__c', 'Reputation_Likelihood_Final__c'].indexOf(target) !== -1 && !refRamRating.$refs.expandRamRating.isOpen) {
        refRamRating.$refs.expandRamRating.toggle()
      } else if ([
        'SIL_Level__c',
        'Design_Intent__c',
        'Initiating_Event_Demand_Scenario__c',
        'Consequence_of_Failure_on_Demand__c',
        'Mitigation_Plan_1__c',
        'Mitigation_Plan_2__c',
        'btnHelpTextRamFinal'].indexOf(target) !== -1 && !refRiskAnalysis.$refs.expandRiskAnalysis.isOpen) {
        refRiskAnalysis.$refs.expandRiskAnalysis.toggle()
      } else if ([
        'TA2_Indicator'].indexOf(target) !== -1 && target.indexOf('TA2_Indicator') !== -1 && !ta2.$refs.expandTa2.isOpen) {
        ta2.$refs.expandTa2.toggle()
      } else if (['I_have_checked_and_confirmed_the_change__c'].indexOf(target) !== -1 && !refReturnToNormal.$refs.expandReturnToNormal.isOpen) {
        refReturnToNormal.$refs.expandReturnToNormal.toggle()
      } else if (['Clearance_Number_Final__c'].indexOf(target) !== -1 && !refReturnFunctionOverride.$refs.expandReturnFunctionOverride.isOpen) {
        refReturnFunctionOverride.$refs.expandReturnFunctionOverride.toggle()
      } else if (['Extend_Require_to_extend__c', 'Not_Extend_Require_to_extend__c'].indexOf(target) !== -1 && !refExtension.$refs.expandExtension.isOpen) {
        refExtension.$refs.expandExtension.toggle()
      }
      this.$nextTick(() => {
        elements[0].focus()
      })
    },
    clickSave () {
      if (this.emoc.OOS__c) {
        this.emoc.Functional_Override_Group__c = 'Group A'
        this.emoc.Functional_Override_Category__c = 'OOS'
      } else if (this.emoc.MOS_for_ops_purpose__c) {
        this.emoc.Functional_Override_Group__c = 'Group A'
        this.emoc.Functional_Override_Category__c = 'MOS for Ops purpose'
      } else if (this.emoc.Inhibit_for_process_or_Equipment_S_D__c) {
        this.emoc.Functional_Override_Group__c = 'Group A'
        this.emoc.Functional_Override_Category__c = 'Inhibit for process or equipment S/D'
      } else if (this.emoc.Inhibit_alarm_for_maintenance__c) {
        this.emoc.Functional_Override_Group__c = 'Group B'
        this.emoc.Functional_Override_Category__c = 'Inhibit alarm for maintenance'
      } else if (this.emoc.Disable_Alarm_by_Ops_for_HW__c) {
        this.emoc.Functional_Override_Group__c = 'Group B'
        this.emoc.Functional_Override_Category__c = 'Disable alarm by Ops for HW'
      } else if (this.emoc.Inhibit_Alarm_request_for_FOX__c) {
        this.emoc.Functional_Override_Group__c = 'Group B'
        this.emoc.Functional_Override_Category__c = 'Inhibit alarm request for FOX'
      } else if (this.emoc.MOS_for_maintenance__c) {
        this.emoc.Functional_Override_Group__c = 'Group C'
        this.emoc.Functional_Override_Category__c = 'MOS for maintenance'
      } else if (this.emoc.Safeguarding_force_signal__c) {
        this.emoc.Functional_Override_Group__c = 'Group C'
        this.emoc.Functional_Override_Category__c = 'Safeguarding force signal'
      }
      Promise.all([
        this.$refs.functionalOverride.$refs.functionalLocationCmp.$validator.validateAll(),
        this.$refs.functionalOverride.$refs.groupCmp.$validator.validateAll(),
        this.$refs.functionalOverride.$refs.changeTypeCmp.$validator.validateAll(),
        this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll(),
        this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll('potentialConsequence'),
        this.$refs.functionalOverride.$refs.prelimRiskAssessmentCmp.$validator.validateAll('mitigationPrecaution'),
        this.$refs.functionalOverride.$refs.ramRatingCmp.$refs.ramRatingInitialCmp.$validator.validateAll(),
        this.showSectionExtension ? this.$refs.functionalOverride.$refs.extensionCmp.$validator.validateAll() : true,
        this.showSectionRiskAssessment === true ? this.checkRamFinal : true,
        this.showSectionRiskAssessment === true ? this.checkRamInitialH : true,
        this.showSectionRiskAssessment === true ? this.$refs.functionalOverride.$refs.riskAnalysisCmp.$validator.validateAll() : true,
        this.showSectionRiskAssessment === true ? this.$refs.functionalOverride.$refs.riskAnalysisCmp.$refs.ramRatingFinalCmp.$validator.validateAll() : true,
        this.showSectionTa2 === true ? this.$refs.functionalOverride.$refs.ta2Cmp.$validator.validateAll() : true,
        this.showSectionReturnToNormal === true ? this.$refs.functionalOverride.$refs.returnToNormalCmp.$validator.validateAll() : true,
        this.showSectionReturnFunctionOverride === true ? this.$refs.functionalOverride.$refs.returnFunctionOverrideCmp.$validator.validateAll() : true,
        !this.checkRequiredGroup,
        !this.checkRequiredChangeType,
        this.showSectionExtension ? !this.checkRequiredExtension : true,
        // this.showSectionTa2 ? !this.checkRequiredTASelectionConfirmed : true,
        this.showSectionReturnToNormal ? !this.checkRequiredReturnToNormal : true
      ]).then((result) => {
        if (result.filter(df => !df).length === 0) {
          this.showPopupLoader()
          let Enddate = (this.emoc.newEndDateTempoHis !== '') ? this.emoc.newEndDateTempoHis : this.emoc.End_date__c
          let emocApp = {
            Id: this.emoc.Id,
            Functional_Location__c: this.emoc.Functional_Location__c,
            Functional_Location_Description__c: this.emoc.Functional_Location_Description__c,
            Equipment__c: this.emoc.Equipment__c,
            Plant_of_Main_Work_Center__c: this.emoc.Plant_of_Main_Work_Center__c,
            Main_Work_Center__c: this.emoc.Main_Work_Center__c,
            Requestor_Name__c: this.emoc.Requestor_Name__c,
            Requestor_ID__c: this.emoc.Requestor_ID__c,
            Department_Resp__c: this.emoc.Department_Resp__c,
            Description__c: this.emoc.Description__c,
            Notification__c: this.emoc.Notification__c,
            Integration_Status__c: this.emoc.Integration_Status__c,
            Notification_Type__c: this.emoc.Notification_Type__c,
            Notification_Status__c: this.emoc.Notification_Status__c,
            OOS__c: this.emoc.OOS__c,
            MOS_for_ops_purpose__c: this.emoc.MOS_for_ops_purpose__c,
            Inhibit_for_process_or_Equipment_S_D__c: this.emoc.Inhibit_for_process_or_Equipment_S_D__c,
            Inhibit_alarm_for_maintenance__c: this.emoc.Inhibit_alarm_for_maintenance__c,
            Disable_Alarm_by_Ops_for_HW__c: this.emoc.Disable_Alarm_by_Ops_for_HW__c,
            Inhibit_Alarm_request_for_FOX__c: this.emoc.Inhibit_Alarm_request_for_FOX__c,
            MOS_for_maintenance__c: this.emoc.MOS_for_maintenance__c,
            Safeguarding_force_signal__c: this.emoc.Safeguarding_force_signal__c,
            Temporary__c: this.emoc.Temporary__c,
            Start_date__c: this.emoc.Start_date__c !== '' ? this.parseDate(this.emoc.Start_date__c) : null,
            End_date__c: Enddate !== '' ? this.parseDate(Enddate) : null,
            // End_date__c: this.emoc.End_date__c !== '' ? this.parseDate(this.emoc.End_date__c) : null,
            Reason_of_Work__c: this.emoc.Reason_of_Work__c,
            Potential_Risk_c__c: this.emoc.Potential_Risk_c__c,
            Other__c: this.emoc.Other__c,
            Assets_Severity__c: this.emoc.Assets_Severity__c,
            Environment_Severity__c: this.emoc.Environment_Severity__c,
            People_Severity__c: this.emoc.People_Severity__c,
            Product_Quality_Severity__c: this.emoc.Product_Quality_Severity__c,
            Reputation_Severity__c: this.emoc.Reputation_Severity__c,
            Assets_Likelihood__c: this.emoc.Assets_Likelihood__c,
            Environment_Likelihood__c: this.emoc.Environment_Likelihood__c,
            People_Likelihood__c: this.emoc.People_Likelihood__c,
            Product_Quality_Likelihood__c: this.emoc.Product_Quality_Likelihood__c,
            Reputation_Likelihood__c: this.emoc.Reputation_Likelihood__c,
            Assets_RAM__c: this.emoc.Assets_RAM__c,
            Environment_RAM__c: this.emoc.Environment_RAM__c,
            People_RAM__c: this.emoc.People_RAM__c,
            Product_Quality_RAM__c: this.emoc.Product_Quality_RAM__c,
            Reputation_RAM__c: this.emoc.Reputation_RAM__c,
            Assets_Remark__c: this.emoc.Assets_Remark__c,
            Environment_Remark__c: this.emoc.Environment_Remark__c,
            People_Remark__c: this.emoc.People_Remark__c,
            Product_Quality_Remark__c: this.emoc.Product_Quality_Remark__c,
            Reputation_Remark__c: this.emoc.Reputation_Remark__c,
            RAM_Total__c: this.emoc.RAM_Total__c,
            Assets_Severity_Final__c: this.emoc.Assets_Severity_Final__c,
            Environment_Severity_Final__c: this.emoc.Environment_Severity_Final__c,
            People_Severity_Final__c: this.emoc.People_Severity_Final__c,
            Product_Quality_Severity_Final__c: this.emoc.Product_Quality_Severity_Final__c,
            Reputation_Severity_Final__c: this.emoc.Reputation_Severity_Final__c,
            Assets_Likelihood_Final__c: this.emoc.Assets_Likelihood_Final__c,
            Environment_Likelihood_Final__c: this.emoc.Environment_Likelihood_Final__c,
            People_Likelihood_Final__c: this.emoc.People_Likelihood_Final__c,
            Product_Quality_Likelihood_Final__c: this.emoc.Product_Quality_Likelihood_Final__c,
            Reputation_Likelihood_Final__c: this.emoc.Reputation_Likelihood_Final__c,
            Assets_RAM_Final__c: this.emoc.Assets_RAM_Final__c,
            Environment_RAM_Final__c: this.emoc.Environment_RAM_Final__c,
            People_RAM_Final__c: this.emoc.People_RAM_Final__c,
            Product_Quality_RAM_Final__c: this.emoc.Product_Quality_RAM_Final__c,
            Reputation_RAM_Final__c: this.emoc.Reputation_RAM_Final__c,
            Assets_Remark_Final__c: this.emoc.Assets_Remark_Final__c,
            Environment_Remark_Final__c: this.emoc.Environment_Remark_Final__c,
            People_Remark_Final__c: this.emoc.People_Remark_Final__c,
            Product_Quality_Remark_Final__c: this.emoc.Product_Quality_Remark_Final__c,
            Reputation_Remark_Final__c: this.emoc.Reputation_Remark_Final__c,
            RAM_Total_Final__c: this.emoc.RAM_Total_Final__c,
            Extend_Require_to_extend__c: this.emoc.Extend_Require_to_extend__c,
            Not_Extend_Require_to_extend__c: this.emoc.Not_Extend_Require_to_extend__c,
            Reason_for_Extension_temporary_MOC__c: this.emoc.Reason_for_Extension_temporary_MOC__c,
            // Clearance_Number__c: this.emoc.Clearance_Number__c,
            // I_have_check__c: this.emoc.I_have_check__c,
            SIL_Level__c: this.emoc.SIL_Level__c,
            Design_Intent__c: this.emoc.Design_Intent__c,
            Initiating_Event_Demand_Scenario__c: this.emoc.Initiating_Event_Demand_Scenario__c,
            Consequence_of_Failure_on_Demand__c: this.emoc.Consequence_of_Failure_on_Demand__c,
            Mitigation_Plan_1__c: this.emoc.Mitigation_Plan_1__c,
            Mitigation_Plan_2__c: this.emoc.Mitigation_Plan_2__c,
            Return_to_Normal__c: this.emoc.Return_to_Normal__c,
            // TA_Selection_confirmed__c: this.emoc.TA_Selection_confirmed__c,
            Work_Clearance_c__c: this.emoc.Work_Clearance_c__c,
            Clearance_Number_Final__c: this.emoc.Clearance_Number_Final__c,
            I_have_checked_and_confirmed_the_change__c: this.emoc.I_have_checked_and_confirmed_the_change__c,
            Clearance_Number_Final_Group_C__c: this.emoc.Clearance_Number_Final_Group_C__c,
            Action_by_FN_closeout__c: this.emoc.Action_by_FN_closeout__c,
            Functional_Override_Group__c: this.emoc.Functional_Override_Group__c,
            Functional_Override_Category__c: this.emoc.Functional_Override_Category__c,
            Status_FN_Report__c: this.emoc.Status_FN_Report__c,
            Action_by_return_to_normal__c: this.emoc.Action_by_return_to_normal__c,
            FN_TA_Review_Date__c: this.emoc.FN_TA_Review_Date__c !== '' ? this.parseDate(this.emoc.FN_TA_Review_Date__c) : null
          }
          let taManMapping = this.$store.state.taManMapping.filter(df => {
            return df.TA2_Indicator__c !== ''
          }).map(dm => {
            return {
              Id: (dm.Id !== '') ? dm.Id : null,
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Position__c: dm.Position__c,
              Require__c: dm.Require__c,
              TA_Level__c: dm.TA_Level__c,
              flgDelete: dm.flgDelete,
              Position_TA3__c: dm.Position_TA3__c,
              TA3_Name_c__c: dm.TA3_Name_c__c,
              TA3_Id__c: dm.TA3_Id__c,
              TA3_Position_c__c: dm.TA3_Position_c__c,
              TA3_Comment__c: dm.TA3_Comment__c,
              TA2_Indicator__c: dm.TA2_Indicator__c,
              TA2_Name__c: dm.TA2_Name__c,
              TA2_Position__c: dm.TA2_Position__c,
              TA2_Expert_Field__c: dm.TA2_Expert_Field__c,
              TA3_Indicator__c: dm.TA3_Indicator__c,
              TA3_Expert_Field__c: dm.TA3_Expert_Field__c,
              TA3_s_review_has_been_completed__c: dm.TA3_s_review_has_been_completed__c,
              Action_by_TA3__c: dm.Action_by_TA3__c
            }
          })
          let potentialRisk = [{
            E_MOC_Application__c: this.emoc.E_MOC_Application__c,
            Topic__c: this.emoc.Potential_Risk_c__c,
            Comment__c: this.emoc.Other__c,
            Type__c: 'Potential Risk'
          }]
          let potentialConsequence = this.$store.state.potentialConsequence.filter(df => {
            return df.Potential_Consequence_c__c !== ''
          }).map(dm => {
            return {
              Id: dm.Id,
              flgDelete: dm.flgDelete,
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Topic__c: dm.Potential_Consequence_c__c,
              Comment__c: dm.Other_Potential_Consequence__c,
              Type__c: 'Potential Consequence'
            }
          })
          let mitigationPrecaution = this.$store.state.mitigationPrecaution.filter(df => {
            return df.Mitigation_Precaution_1__c !== ''
          }).map(dm => {
            return {
              Id: dm.Id,
              flgDelete: dm.flgDelete,
              E_MOC_Application__c: dm.E_MOC_Application__c,
              Topic__c: dm.Mitigation_Precaution_1__c,
              Comment__c: dm.Other_Mitigation__c,
              Type__c: 'Mitigation/Precaution'
            }
          })
          let emocTemporaryHistory = { // emocTemporaryHistory
            Id: null,
            Action_by__c: this.emoc.User_Login,
            E_MOC_Application__c: this.emoc.Id,
            Yes__c: this.emoc.Comfirm,
            Type__c: 'Extend FN',
            Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            Old_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.End_date__Old) : null,
            End_Date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null,
            New_require_end_date__c: (this.emoc.Comfirm === true) ? this.parseDate(this.emoc.newEndDateTempoHis) : null
          }
          let paramSave = {
            emocApp: emocApp,
            ta2Man: taManMapping,
            potentialRisk: potentialRisk,
            potentialConsequence: potentialConsequence,
            mitigationPrecaution: mitigationPrecaution,
            emocTemporaryHistory: emocTemporaryHistory
          }
          console.log('paramSave', paramSave)
          this.$store.dispatch('saveCreateEmoc', JSON.stringify(paramSave))
            .then(result => {
              this.closePopupLoader()
              if (result.statusSuccess) {
                this.showPopup()
                this.resultSuccess = result
              }
            })
          this.hasError = false
        } else {
          this.errorBagItems = Object.assign([], this.errors.items)
          if (this.checkRequiredGroup) {
            this.errorBagItems.push({field: 'OOS__c'})
          }
          if (this.checkRequiredChangeType) {
            this.errorBagItems.push({field: 'Temporary__c'})
          }
          if (this.checkRequiredExtension) {
            this.errorBagItems.push({field: 'Extend_Require_to_extend__c'})
          }
          // if (this.checkRequiredTASelectionConfirmed) {
          //   this.errorBagItems.push({field: 'TA_Selection_confirmed__c'})
          // }
          if (this.checkRequiredReturnToNormal) {
            this.errorBagItems.push({field: 'I_have_checked_and_confirmed_the_change__c'})
          }
          if (!this.checkRamFinal || !this.checkRamInitialH) {
            this.errorBagItems.push({field: 'btnHelpTextRamFinal'})
          }
          this.hasError = true
          this.$refs.popoverErrorStd.closePopover()
        }
      })
    },
    parseDate (date) {
      if (!date) return null
      const [day, month, year] = date.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    closePopupSuccess () {
      this.$store.dispatch('sendMsg', {
        action: 'redirectToDetailPage',
        emocApplicationId: this.resultSuccess.emocApplicationId
      })
    },
    showPopupLoader () {
      this.$refs.popupLoader.onOpen()
    },
    closePopupLoader () {
      this.$refs.popupLoader.onClose()
    }
  },
  components: {
    functionalOverride
  }
}
</script>

<style src="../assets/style.css"></style>


