<template>
  <div name="ram-rating">
    <expandable-section-std title="RAM Rating" :closedOnStart="false" ref="expandRamRating">
      <div class="slds-border_bottom topic2">
        <label>Initial Risk</label>
      </div>
      <img src="../assets/RAM_Rating.png" class="image-ram-rating" />

      <ram-rating-initial :emoc="emoc"
                          :assetsSeverityOption="assetsSeverityOption"
                          :environmentSeverityOption="environmentSeverityOption"
                          :peopleSeverityOption="peopleSeverityOption"
                          :productQualitySeverityOption="productQualitySeverityOption"
                          :reputationSeverityOption="reputationSeverityOption"
                          :likelihoodOption="likelihoodOption"
                          :ramReq="ramInitialReq"
                          :assetsRam="assetsRam"
                          :environmentRam="environmentRam"
                          :peopleRam="peopleRam"
                          :productQualityRam="productQualityRam"
                          :reputationRam="reputationRam"
                          :totalRam="totalRam"
                          :windowWidth="windowWidth"
                          :disabledNextStaEmocRegis="disabledNextStaEmocRegis"
                          ref="ramRatingInitialCmp" />

    </expandable-section-std>
  </div>
</template>
<script>
import ramRatingInitial from './RamRatingInitial.vue'

export default {
  name: 'ram-rating',
  props: [
    'emoc',
    'disabledNextStaEmocRegis'
  ],
  data () {
    return {
      assetsSeverityOption: [
        {value: '', label: '--None--'},
        {value: '0 - No damage', label: '0 - No damage'},
        {value: '1 - Slight damage <= 10 KUSD', label: '1 - Slight damage <= 10 KUSD'},
        {value: '2 - Minor damage 10-100 KUSD', label: '2 - Minor damage 10-100 KUSD'},
        {value: '3 - Localised damage 0.1-1 MUSD', label: '3 - Localised damage 0.1-1 MUSD'},
        {value: '4 - Major damage 1-10 MUSD', label: '4 - Major damage 1-10 MUSD'},
        {value: '5 - Extensive damage > 10 MUSD', label: '5 - Extensive damage > 10 MUSD'}
      ],
      environmentSeverityOption: [
        {value: '', label: '--None--'},
        {value: '0 - No effect', label: '0 - No effect'},
        {value: '1 - Slight effect, within fence', label: '1 - Slight effect, within fence'},
        {value: '2 - Minor effect, single breach or complaint', label: '2 - Minor effect, single breach or complaint'},
        {value: '3 - Localised effect, repeated breaches or many complaints', label: '3 - Localised effect, repeated breaches or many complaints'},
        {value: '4 - Major effect, extended breach or wide spread nuisance', label: '4 - Major effect, extended breach or wide spread nuisance'},
        {value: '5 - Massive effect, persistent severe damage', label: '5 - Massive effect, persistent severe damage'}
      ],
      peopleSeverityOption: [
        {value: '', label: '--None--'},
        {value: '0 - No health effect/injury', label: '0 - No health effect/injury'},
        {value: '1 - Slight health effect/injury (FAC)', label: '1 - Slight health effect/injury (FAC)'},
        {value: '2 - Minor health effect/injury (MTC)', label: '2 - Minor health effect/injury (MTC)'},
        {value: '3 - Major health effect/injury (LWC)', label: '3 - Major health effect/injury (LWC)'},
        {value: '4 - Permanent Total Disability or 1 to 3 fatalities', label: '4 - Permanent Total Disability or 1 to 3 fatalities'},
        {value: '5 - Multiple fatalities', label: '5 - Multiple fatalities'}
      ],
      productQualitySeverityOption: [
        {value: '', label: '--None--'},
        {value: '0 - No effect', label: '0 - No effect'},
        {value: '1 - Slight effect', label: '1 - Slight effect'},
        {value: '2 - Limited effect', label: '2 - Limited effect'},
        {value: '3 - Considerable effect', label: '3 - Considerable effect'},
        {value: '4 - Major effect', label: '4 - Major effect'},
        {value: '5 - Massive effect', label: '5 - Massive effect'}
      ],
      reputationSeverityOption: [
        {value: '', label: '--None--'},
        {value: '0 - No impact', label: '0 - No impact'},
        {value: '1 - Slight impact', label: '1 - Slight impact'},
        {value: '2 - Limited impact, Local Media', label: '2 - Limited impact, Local Media'},
        {value: '3 - Considerable impact, Regional media', label: '3 - Considerable impact, Regional media'},
        {value: '4 - National impact', label: '4 - National impact'},
        {value: '5 - International impact', label: '5 - International impact'}
      ],
      likelihoodOption: [
        {value: '', label: '--None--'},
        {value: 'A - Improbable', label: 'A - Improbable'},
        {value: 'B - Unlikely', label: 'B - Unlikely'},
        {value: 'C - Possible', label: 'C - Possible'},
        {value: 'D - Likely', label: 'D - Likely'},
        {value: 'E - High', label: 'E - High'}
      ],
      windowWidth: window.innerWidth,
      ramRatingTable: {
        '5': {'A': 'M', 'B': 'M', 'C': 'H', 'D': 'H', 'E': 'H'},
        '4': {'A': 'L', 'B': 'M', 'C': 'M', 'D': 'H', 'E': 'H'},
        '3': {'A': 'L', 'B': 'L', 'C': 'M', 'D': 'M', 'E': 'H'},
        '2': {'A': 'L', 'B': 'L', 'C': 'L', 'D': 'M', 'E': 'M'},
        '1': {'A': 'L/N', 'B': 'L/N', 'C': 'L', 'D': 'L', 'E': 'L'},
        '0': {'A': 'L/N', 'B': 'L/N', 'C': 'L/N', 'D': 'L/N', 'E': 'L/N'}
      },
      ramRatingScore: {'L/N': 1, 'L': 2, 'M': 3, 'H': 4},
      arrayScore: ['', 'L/N', 'L', 'M', 'H']
    }
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize)
    })
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResize)
  },
  computed: {
    ramInitialReq () {
      // return this.emoc.Status_Functional_Override__c === 'Create'
      return true
    },
    // ramFinalReq () {
    //   return this.emoc.Status_Common_Workflow__c === 'Hazard Screening'
    // },
    assetsRam () {
      let severityScore = this.emoc.Assets_Severity__c ? this.emoc.Assets_Severity__c.substring(0, 1) : ''
      let likelihoodScore = this.emoc.Assets_Likelihood__c ? this.emoc.Assets_Likelihood__c.substring(0, 1) : ''
      if (severityScore !== '' && likelihoodScore !== '') {
        return this.ramRatingTable[severityScore][likelihoodScore]
      } else {
        return ''
      }
    },
    environmentRam () {
      let severityScore = this.emoc.Environment_Severity__c ? this.emoc.Environment_Severity__c.substring(0, 1) : ''
      let likelihoodScore = this.emoc.Environment_Likelihood__c ? this.emoc.Environment_Likelihood__c.substring(0, 1) : ''
      if (severityScore !== '' && likelihoodScore !== '') {
        return this.ramRatingTable[severityScore][likelihoodScore]
      } else {
        return ''
      }
    },
    peopleRam () {
      let severityScore = this.emoc.People_Severity__c ? this.emoc.People_Severity__c.substring(0, 1) : ''
      let likelihoodScore = this.emoc.People_Likelihood__c ? this.emoc.People_Likelihood__c.substring(0, 1) : ''
      if (severityScore !== '' && likelihoodScore !== '') {
        return this.ramRatingTable[severityScore][likelihoodScore]
      } else {
        return ''
      }
    },
    productQualityRam () {
      let severityScore = this.emoc.Product_Quality_Severity__c ? this.emoc.Product_Quality_Severity__c.substring(0, 1) : ''
      let likelihoodScore = this.emoc.Product_Quality_Likelihood__c ? this.emoc.Product_Quality_Likelihood__c.substring(0, 1) : ''
      if (severityScore !== '' && likelihoodScore !== '') {
        return this.ramRatingTable[severityScore][likelihoodScore]
      } else {
        return ''
      }
    },
    reputationRam () {
      let severityScore = this.emoc.Reputation_Severity__c ? this.emoc.Reputation_Severity__c.substring(0, 1) : ''
      let likelihoodScore = this.emoc.Reputation_Likelihood__c ? this.emoc.Reputation_Likelihood__c.substring(0, 1) : ''
      if (severityScore !== '' && likelihoodScore !== '') {
        return this.ramRatingTable[severityScore][likelihoodScore]
      } else {
        return ''
      }
    },
    totalRam () {
      let indexScore = [
        this.assetsRam !== '' ? this.ramRatingScore[this.assetsRam] : 0,
        this.environmentRam !== '' ? this.ramRatingScore[this.environmentRam] : 0,
        this.peopleRam !== '' ? this.ramRatingScore[this.peopleRam] : 0,
        this.productQualityRam !== '' ? this.ramRatingScore[this.productQualityRam] : 0,
        this.reputationRam !== '' ? this.ramRatingScore[this.reputationRam] : 0
      ]
      indexScore.sort(function (a, b) { return b - a })
      return this.arrayScore[indexScore[0]]
    }
  },
  watch: {
    assetsRam (newVal) {
      this.emoc.Assets_RAM__c = newVal
    },
    environmentRam (newVal) {
      this.emoc.Environment_RAM__c = newVal
    },
    peopleRam (newVal) {
      this.emoc.People_RAM__c = newVal
    },
    productQualityRam (newVal) {
      this.emoc.Product_Quality_RAM__c = newVal
    },
    reputationRam (newVal) {
      this.emoc.Reputation_RAM__c = newVal
    },
    totalRam (newVal) {
      this.emoc.RAM_Total__c = newVal
    }
  },
  methods: {
    onResize () {
      this.windowWidth = window.innerWidth
    }
  },
  components: {
    ramRatingInitial
  }
}
</script>
<style>
  .topic {
    font-size: large;
    padding-top: 1rem;
    margin-bottom: 0.5rem;
    color: #4282d7;
  }
  .topic2 {
    font-size: large;
    padding-top: 0.5rem;
    margin-bottom: 0.5rem;
    color: #212121; 
  }
</style>


