import Vue from 'vue'
import Vuex from 'vuex'
import createEmocApi from '../api/createEmoc'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    lookupFncLocation: {},
    lookupUser: {},
    lookupTa3: {},
    ta3ManMaster: {},
    lookupClearanceNumber: [],
    emoc: {
      Id: null,
      Status_Functional_Override__c: '',
      Functional_Location__c: '',
      Functional_Location__r: {
        Id: '',
        Name: ''
      },
      Functional_Location_Description__c: '',
      Equipment__c: '',
      Plant_of_Main_Work_Center__c: '',
      Main_Work_Center__c: '',
      Requestor_Name__c: '',
      Department_Resp__c: '',
      Indicator__c: '',
      Requestor_ID__c: '',
      Requester__c: '',
      Description__c: '',
      Notification__c: '',
      Integration_Status__c: '',
      Notification_Type__c: '',
      Notification_Status__c: '',
      Temporary__c: false,
      Start_date__c: '',
      End_date__c: '',
      Reason_of_Work__c: '', // Fn
      Potential_Risk_c__c: '',
      Other__c: '',
      Potential_Consequence_c__c: '',
      Other_Potential_Consequence__c: '',
      Mitigation_Precaution_1__c: '',
      Other_Mitigation__c: '',
      RAM_Rating__c: '',
      Assets_Severity__c: '',
      Environment_Severity__c: '',
      People_Severity__c: '',
      Product_Quality_Severity__c: '',
      Reputation_Severity__c: '',
      Assets_Likelihood__c: '',
      Environment_Likelihood__c: '',
      People_Likelihood__c: '',
      Product_Quality_Likelihood__c: '',
      Reputation_Likelihood__c: '',
      Assets_RAM__c: '',
      Environment_RAM__c: '',
      People_RAM__c: '',
      Product_Quality_RAM__c: '',
      Reputation_RAM__c: '',
      Assets_Remark__c: '',
      Environment_Remark__c: '',
      People_Remark__c: '',
      Product_Quality_Remark__c: '',
      Reputation_Remark__c: '',
      RAM_Total__c: '',
      OOS__c: false,
      MOS_for_ops_purpose__c: false,
      Inhibit_for_process_or_Equipment_S_D__c: false,
      Inhibit_alarm_for_maintenance__c: false,
      Disable_Alarm_by_Ops_for_HW__c: false,
      Inhibit_Alarm_request_for_FOX__c: false,
      MOS_for_maintenance__c: false,
      Safeguarding_force_signal__c: false,
      Doc_Req_Result: 0,
      MOC_Focal_Point_Name__c: '',
      MOC_Focal_Point_ID__c: '',
      Position__c: '',
      Budget_Estimate_Initial__c: '',
      Budget_Estimate_Final__c: '',
      Hazard_Src_Result: 0,
      HAZOP__c: false,
      HRA__c: false,
      Environmental_Impact__c: false,
      Reliability_Checklist__c: false,
      Assets_Severity_Final__c: '',
      Environment_Severity_Final__c: '',
      People_Severity_Final__c: '',
      Product_Quality_Severity_Final__c: '',
      Reputation_Severity_Final__c: '',
      Assets_Likelihood_Final__c: '',
      Environment_Likelihood_Final__c: '',
      People_Likelihood_Final__c: '',
      Product_Quality_Likelihood_Final__c: '',
      Reputation_Likelihood_Final__c: '',
      Assets_RAM_Final__c: '',
      Environment_RAM_Final__c: '',
      People_RAM_Final__c: '',
      Product_Quality_RAM_Final__c: '',
      Reputation_RAM_Final__c: '',
      Assets_Remark_Final__c: '',
      Environment_Remark_Final__c: '',
      People_Remark_Final__c: '',
      Product_Quality_Remark_Final__c: '',
      Reputation_Remark_Final__c: '',
      RAM_Total_Final__c: '',
      Show_TA_Mandatory__c: false,
      Extend_Require_to_extend__c: false,
      Not_Extend_Require_to_extend__c: false,
      Reason_for_Extension_temporary_MOC__c: '',
      // Clearance_Number__c: '',
      // I_have_check__c: false,
      SIL_Level__c: '',
      Design_Intent__c: '',
      Initiating_Event_Demand_Scenario__c: '',
      Consequence_of_Failure_on_Demand__c: '',
      Mitigation_Plan_1__c: '',
      Mitigation_Plan_2__c: '',
      Return_to_Normal__c: false,
      TA_Selection_confirmed__c: false,
      Work_Clearance_c__c: '',
      Clearance_Number_Final__c: '',
      I_have_checked_and_confirmed_the_change__c: false,
      Clearance_Number_Final_Group_C__c: '',
      TA3_Comment__c: '',
      Show_TA_section__c: false,
      Action_by_FN_closeout__c: '',
      Comfirm: false,
      Functional_Override_Group__c: '',
      Functional_Override_Category__c: '',
      Status_FN_Report__c: '',
      User_Login: '',
      Action_by_return_to_normal__c: '',
      End_date__Old: '', // สร้างเอง dup End_date__c
      newEndDateTempoHis: '', // สร้างเอง
      FN_TA_Review_Date__c: '',
      flgClearanceNumberFinal: false // สร้างเอง เช็ค Clearance_Number_Final__c ไม่ว่าง = true
    },
    taManMaster: [],
    taManMapping: [],
    taManMappingLength: 0,
    potentialRiskList: [],
    potentialConsequenceList: [],
    potentialConsequence: [],
    potentialConsequenceLength: 0,
    mitigationPrecautionList: [],
    mitigationPrecaution: [],
    mitigationPrecautionLength: 0,
    btnComfirmDisable: false
  },
  getters: {
    getLookupFncLocation: (state) => {
      return state.lookupFncLocation
    },
    getLookupUser: (state) => {
      return state.lookupUser
    }
  },
  mutations: {
    setLookupFncLocation: (state, {type, data}) => {
      let temp = state.lookupFncLocation
      temp[type] = data
      state.lookupFncLocation = JSON.parse(JSON.stringify(temp))
    },
    setLookupUser: (state, {type, data}) => {
      let temp = state.lookupUser
      temp[type] = data
      state.lookupUser = JSON.parse(JSON.stringify(temp))
    },
    setLookupTa3: (state, { result, position }) => {
      state.lookupTa3[position] = result
    },
    setTa3ManMaster: (state, { result, position }) => {
      state.ta3ManMaster[position] = result
    },
    setLookupClearanceNumber: (state, data) => {
      state.lookupClearanceNumber = data
    },
    setEmocApplication: (state, data) => {
      let setDefaultField = data
      state.emoc = Object.assign(state.emoc, setDefaultField)
    },
    setTaManMaster: (state, data) => {
      state.taManMaster = data
    },
    // setTaManMapping: (state, data) => {
    //   state.taManMapping = data
    //   state.taManMappingLength = data.length
    //   let setLookupTa3Temp = {}
    //   data.map(dm => {
    //     setLookupTa3Temp[dm.Position__c] = []
    //   })
    //   state.lookupTa3 = setLookupTa3Temp
    // },
    setTaManMapping: (state, data) => {
      state.taManMapping = data
      state.taManMappingLength = data.length
      let setTa3ManMasterTemp = {}
      data.map(dm => {
        setTa3ManMasterTemp[dm.Position__c] = []
      })
      state.ta3ManMaster = setTa3ManMasterTemp
    },
    setPotentialRiskList: (state, data) => {
      state.potentialRiskList = data
    },
    setPotentialConsequenceList: (state, data) => {
      state.potentialConsequenceList = data
    },
    setPotentialConsequence: (state, data) => {
      state.potentialConsequence = data
      state.potentialConsequenceLength = data.length
      let setPotentialConTemp = {}
      data.map(dm => {
        setPotentialConTemp[dm.Potential_Consequence_c__c] = []
      })
      state.potentialCon = setPotentialConTemp
    },
    setMitigationPrecautionList: (state, data) => {
      state.mitigationPrecautionList = data
    },
    setMitigationPrecaution: (state, data) => {
      state.mitigationPrecaution = data
      state.mitigationPrecautionLength = data.length
      let setMitigationTemp = {}
      data.map(dm => {
        setMitigationTemp[dm.Mitigation_Precaution_1__c] = []
      })
      state.mitigation = setMitigationTemp
    },
    setBtnComfirmDisable: (state, data) => {
      state.btnComfirmDisable = data
    }
  },
  actions: {
    receiveLookupFncLocation: ({commit}, {type, sendParam}) => {
      createEmocApi.getLookupFncLocation(result => {
        commit('setLookupFncLocation', {type, data: result})
      }, sendParam)
    },
    receiveLookupUser: ({commit}, {type, sendParam}) => {
      createEmocApi.getLookupUser(result => {
        commit('setLookupUser', {type, data: result})
      }, sendParam)
    },
    receiveLookupTa3: ({commit}, { sendParam, position }) => {
      createEmocApi.getLookupTa3(result => {
        commit('setLookupTa3', { result, position })
      }, sendParam)
    },
    receiveTa3ManMaster: ({commit}, { sendParam, position }) => {
      let paramSearch = {
        Indicator: sendParam,
        Section: position
      }
      createEmocApi.getTa3ManMaster(result => {
        commit('setTa3ManMaster', { result, position })
      }, paramSearch)
    },
    receiveLookupClearanceNumber: ({commit}, sendParam) => {
      createEmocApi.getLookupClearanceNumber(result => {
        commit('setLookupClearanceNumber', result)
      }, sendParam)
    },
    receiveEmocApplication: ({commit}, sendParam) => {
      createEmocApi.getEmocApplication(result => {
        console.log('result', result)
        commit('setEmocApplication', result)
      }, sendParam)
    },
    receiveTaManMaster: ({commit}, sendParam) => {
      createEmocApi.getTaManMaster(result => {
        // console.log('setTaManMaster', result)
        commit('setTaManMaster', result)
      }, sendParam)
    },
    receiveTaManMapping: ({dispatch, commit}, sendParam) => {
      createEmocApi.getTaManMapping(result => {
        commit('setTaManMapping', result)
        // console.log('setTaManMapping', result)
        if (result.length > 0) {
          result.map(dm => {
            // dispatch('receiveLookupTa3', { sendParam: dm.TA3_Name_c__c, position: dm.Position__c })
            dispatch('receiveTa3ManMaster', { sendParam: dm.TA3_Indicator__c, position: dm.Position__c })
          })
        }
      }, sendParam)
    },
    saveCreateEmoc: ({commit}, paramSave) => {
      return new Promise((resolve, reject) => {
        createEmocApi.saveCreateEmoc(result => {
          resolve(result)
        }, paramSave)
      })
    },
    sendMsg: ({ commit }, paramMsg) => {
      createEmocApi.sendMsg(result => {
        if (result) {
          console.log('sendMsg successfully', result)
        }
      }, paramMsg)
    },
    receivePotentialRiskList: ({commit}) => {
      createEmocApi.getPotentialRiskList(result => {
        commit('setPotentialRiskList', result)
      })
    },
    receivePotentialConsequenceList: ({commit}) => {
      createEmocApi.getPotentialConsequenceList(result => {
        commit('setPotentialConsequenceList', result)
      })
    },
    receivePotentialConsequence: ({dispatch, commit}, sendParam) => {
      createEmocApi.getPotentialConsequence(result => {
        commit('setPotentialConsequence', result)
      }, sendParam)
    },
    receiveMitigationPrecautionList: ({commit}) => {
      createEmocApi.getMitigationPrecautionList(result => {
        commit('setMitigationPrecautionList', result)
      })
    },
    receiveMitigationPrecaution: ({dispatch, commit}, sendParam) => {
      createEmocApi.getMitigationPrecaution(result => {
        commit('setMitigationPrecaution', result)
      }, sendParam)
    }
  }
})



